body {
  margin: 0;
	text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	color: #333;
}

code {
  	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
	font-family: 'Sonsie One', sans-serif;
	font-size: calc(1rem + 5vmin);
	color: purple;
	margin-top: 15px;
	transform: scaleX(.7);
}

span > img {
	margin-bottom: -20px;
	padding: 0 15px 0 15px;
	clip-path: circle(60px at 72px 60px);
	transform: scaleX(1.3);
}

.flex {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	max-width: 1700px;
	margin: 0 auto;
}

.card {
	background-color: lightblue;
	width: 250px;
	padding: 20px;
	margin: 10px;
	border-radius: 25px 0px 25px 0;
}

.card:hover {
	animation: grow 200ms linear forwards;
}

@keyframes grow {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(1.02);
	}
}

.card-img {
	margin: -25px 0 0 -25px;
	width: 110%;
}

.email-addy {
	margin-top: -10px;
	font-size: .9rem;
}

.search-box-container {
	padding: 20px 0 50px 0;
	margin-top: -20px;
}

input {
			height: 40px;
			width: 200px;
			border: 1px dotted purple;
			border-radius: 4px;
			padding-left: 7px;
		}

